export default {
	onLoad:function(e){
		this.PickOrderCode=e.PickOrderCode
	},
	data() {
		return {
			WmsPickAreaDetail: {
				
			},
			labelFoucs: true,
		    PickOrderCode:'',
			LastMaterialLabel:''
		}
	},
	methods: {
		getMaterialLabelInfo() {
			this.api.baseRequest({
				url:  'PreparationView/IsTheReelIdInPickOrder',
				method: "get",
				data: {
					ReelId: this.WmsPickAreaDetail.ReelId,
					PickOrderCode: this.PickOrderCode
				},
				success: res => {
					var resData = JSON.parse(res.data.Content)
					if (resData.Success) {
						this.WmsPickAreaDetail = resData.Data
					} else {
						uni.showModal({
							content: resData.Message,
							showCancel: false
						});
						this.WmsPickAreaDetail={
							ReelId:''
						}
						this.labelFoucs=true
						return
					}

				},
				fail: () => {

				},
				complete: () => {
                       
				}
			})

		},
		ConfirmCheck() {
			//uni.getStorageSync('')
			this.api.baseRequest({
				url:  'PreparationView/ConfirmReelToSend',
				method: "post",
				data: this.WmsPickAreaDetail,
				success: res => {
					var resData = JSON.parse(res.data.Content)
					if (resData.Success) {
						this.WmsPickAreaDetail = {
						   ReelId:''
						}
					} else {
						uni.showModal({
							content: resData.Message,
							showCancel: false
						});
						this.WmsPickAreaDetail.NewLocationCode=''
						this.newLocationFouce=true
						
						return
					}

				},
				fail: () => {

				},
				complete: () => {

				}
			})

		},
		CancelCheck(){
			uni.navigateBack({
				
			})
		},
		CancelLabel(){
			if (this.WmsPickAreaDetail.ReelId !='') {
				uni.showModal({
					title: '提示',
					content: '是否作废此条码',
					success: function(res) {
						if (res.confirm) {
							this.api.baseRequest({
								url:  'PreparationView/CancelPickMaterialLabel',
								method: "post",
								data: this.WmsPickAreaDetail,
								success: result => {
									var resData = JSON.parse(result.data.Content)
									if (resData.Success) {
							        	this.WmsPickAreaDetail = {
										   ReelId:''
										}
										uni.showModal({
											content: resData.Message,
											showCancel: false
										});
									} else {
										uni.showModal({
											content: resData.Message,
											showCancel: false
										});
										return
									}
			
								},
								fail: () => {
			
								},
								complete: () => {
			
								}
							})
						} else if (res.cancel) {
			
						}
					}.bind(this)
				})
			
			}else{
				uni.showModal({
					content: '没有可作废的条码',
					showCancel: false
				});
			}
		}
	}
}
